import React from "react";
import { Formik } from "formik";
import { Input, Modal } from "antd";

const ResetPassword = ({ forgotPwdModalVisible, setForgotPwdModalVisible }) => {
  const validateForm = (values) => {
    const errors = {};

    if (!values.email) {
      errors.email = "Email is required";
    } else if (
      !/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
        String(values.email).toLowerCase()
      )
    ) {
      errors.email = "Please enter a valid email";
    }
  };
  const handleSubmit = (values) => {
    validateForm(values);
    // resetPassword(values)
  };

  return (
    <div class="vh-80">
      <Modal
        destroyOnClose={true}
        visible={forgotPwdModalVisible}
        onCancel={() => setForgotPwdModalVisible(false)}
        footer={null}
        title={
          <>
            <div
              className="d-flex flex-row align-items-center justify-content-center"
              style={{ height: "37px" }}
            >
              <div class="bs-icon-md bs-icon-circle bs-icon-primary bs-icon my-4">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="1em"
                  height="1em"
                  fill="currentColor"
                  viewBox="0 0 16 16"
                  class="bi bi-person"
                >
                  <path d="M8 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm2-3a2 2 0 1 1-4 0 2 2 0 0 1 4 0zm4 8c0 1-1 1-1 1H3s-1 0-1-1 1-4 6-4 6 3 6 4zm-1-.004c-.001-.246-.154-.986-.832-1.664C11.516 10.68 10.289 10 8 10c-2.29 0-3.516.68-4.168 1.332-.678.678-.83 1.418-.832 1.664h10z"></path>
                </svg>
              </div>
              <h2 class="text-uppercase fw-bold fs-4 ms-3">Reset Password</h2>
            </div>
          </>
        }
      >
        <div>
          <Formik
            initialValues={{
              email: "",
            }}
            validate={validateForm}
            onSubmit={handleSubmit}
          >
            {({
              values,
              errors,
              touched,
              handleChange,
              handleBlur,
              handleSubmit,
              setFieldValue,
              setFieldTouched,
              isSubmitting,
            }) => {
              return (
                <>
                  <div className="formik-field_wrap row auth_form">
                    <div className="formik-field-left col col-lg-12 col-12 auth_field mb-3">
                      Email
                      <Input
                        style={{ padding: "10px 15px" }}
                        id="email"
                        placeholder="Email"
                        value={values.email}
                        onChange={handleChange}
                        onBlur={handleBlur}
                      className="form-control"

                      />
                      {errors.email && touched.email ? (
                        <p
                          style={{
                            color: "red",
                            fontSize: "small",
                            margin: "0",
                          }}
                        >
                          {errors.email}
                        </p>
                      ) : null}
                    </div>
                    <p>
                      Enter the email address associated with your account, and
                      we'll email you a link to reset your password.
                      <br />
                    </p>
                  </div>
                  <div className="col col-12 text-center">
                    <button
                      type="submit"
                      className="btn d-block w-100 btn-outline-primary py-3"
                      onClick={handleSubmit}
                    >
                      <i className="fa-solid fa-circle-check me-2"></i>Submit
                    </button>
                  </div>
                </>
              );
            }}
          </Formik>
        </div>
      </Modal>
      {/* <section class="position-relative py-4 py-xl-5">
        <div class="container">
          <div class="row d-flex justify-content-center">
            <div class="col-md-6 col-xl-4">
              <div class="card my-5">
                <div class="card-body d-flex flex-column align-items-center">
                  <div class="bs-icon-xl bs-icon-circle bs-icon-primary bs-icon my-4">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      width="1em"
                      height="1em"
                      fill="currentColor"
                      viewBox="0 0 16 16"
                      class="bi bi-person"
                    >
                      <path d="M8 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm2-3a2 2 0 1 1-4 0 2 2 0 0 1 4 0zm4 8c0 1-1 1-1 1H3s-1 0-1-1 1-4 6-4 6 3 6 4zm-1-.004c-.001-.246-.154-.986-.832-1.664C11.516 10.68 10.289 10 8 10c-2.29 0-3.516.68-4.168 1.332-.678.678-.83 1.418-.832 1.664h10z"></path>
                    </svg>
                  </div>
                  <h2 class="text-uppercase fw-bold">Reset Password</h2>
                  <form class="text-center auth-form" method="post">
                    <div class="mb-3">
                      <input
                        class="form-control"
                        type="email"
                        name="email"
                        placeholder="Email"
                        required=""
                      />
                    </div>
                    <p>
                      Enter the email address associated with your account, and
                      we'll email you a link to reset your password.
                      <br />
                    </p>
                    <div class="mb-3">
                      <button
                        class="btn d-block w-100 btn-outline-primary py-3"
                        type="submit"
                      >
                        Reset Password
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section> */}
    </div>
  );
};

export default ResetPassword;