import React from "react";
import { Formik } from "formik";
import { Input, Modal } from "antd";
import { notification } from "antd";
import { useHistory } from "react-router";
import { useDispatch } from "react-redux";
import { adminLogin } from "../../Graphs/Admin/login";
import { setAuthToken, setLoginFlag, setLoginUserData } from "../../Actions/adminAuth";

const Login = ({
  loginModalVisible,
  setLoginModalVisible,
  handleForgotPasswordModal,
}) => {
  const history = useHistory();
  const dispatch = useDispatch();
  const validateForm = (values) => {
    const errors = {};

    if (!values.email) {
      errors.email = "Username is required";
    }

    if (!values.password) {
      errors.password = "Password is required";
    } else if (values.password !== "" && values.password.length < 6) {
      errors.password = "At least 8 characters required";
    }

    console.log("Validating errors -- ", errors);

    return errors;
  };

  const doLogin = async(values) =>{
    await adminLogin(values.email, values.password)
    .then((res)=>{
      if(res.status === 200){
        console.log(res);
        notification["success"]({
          message: "Logged In Successfully",
        });
        dispatch(setLoginFlag(true));
        dispatch(setAuthToken(res?.data?.token))
        dispatch(setLoginUserData(res?.data?.user));
        history.push("/home");
      }else {
        notification["error"]({
          message: "Log In Failed",
        });
      }
    }).catch((err)=>{
      notification["error"]({
        message: "Log In Failed",
      });
    })
  }

  const handleSubmit = async (values, action) => {
    validateForm(values);
    doLogin(values);
    

  };

  return (
    <div>
      <Modal
        destroyOnClose={true}
        visible={loginModalVisible}
        onCancel={() => setLoginModalVisible(false)}
        footer={null}
        title={
          <>
           <div className="d-flex flex-row align-items-center justify-content-center" style={{height:"37px"}}>
            <div className="bs-icon-md bs-icon-circle bs-icon-primary bs-icon my-4">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="1em"
                height="1em"
                fill="currentColor"
                viewBox="0 0 16 16"
                className="bi bi-person"
              >
                <path d="M8 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm2-3a2 2 0 1 1-4 0 2 2 0 0 1 4 0zm4 8c0 1-1 1-1 1H3s-1 0-1-1 1-4 6-4 6 3 6 4zm-1-.004c-.001-.246-.154-.986-.832-1.664C11.516 10.68 10.289 10 8 10c-2.29 0-3.516.68-4.168 1.332-.678.678-.83 1.418-.832 1.664h10z"></path>
              </svg>
            </div>
            <h2 className="text-uppercase fw-bold fs-4 ms-3">Log in</h2>
            </div>
          </>
        }
      >
        <Formik
          initialValues={{
            email: "",
            password: "",
            rememberMe: false,
          }}
          validate={validateForm}
          onSubmit={handleSubmit}
        >
          {({
            values,
            errors,
            touched,
            handleChange,
            handleBlur,
            handleSubmit,
            setFieldValue,
            setFieldTouched,
            isSubmitting,
          }) => {
            return (
              <div>
                
                <div className="formik-field_wrap row auth_form">
                  <div className="formik-field-left col col-12 auth_field">
                    Username
                    <Input
                      style={{ padding: "10px 15px" }}
                      id="email"
                      placeholder="email"
                      value={values.email}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      className="form-control"

                    />
                    {errors.email && touched.email ? (
                      <p
                        style={{
                          color: "red",
                          fontSize: "small",
                          margin: "0",
                        }}
                      >
                        {errors.email}
                      </p>
                    ) : null}
                  </div>
                </div>

                <div className="mt-3 formik-field_wrap row auth_form">
                  <div className="formik-field-left col col-12 auth_field">
                    Password
                    <Input
                      style={{ padding: "10px 15px" }}
                      type="password"
                      id="password"
                      placeholder="Password"
                      value={values.password}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      className="form-control"
                    />
                    {errors.password && touched.password ? (
                      <p
                        style={{
                          color: "red",
                          fontSize: "small",
                          margin: "0",
                        }}
                      >
                        {errors.password}
                      </p>
                    ) : null}
                  </div>
                </div>
                <div className="mt-3 row justify-content-between">
                  <div className="col col-6 mb-3 pr-0">
                    <div className="form-check my-2">
                      <Input
                        // style={{ padding: "10px 15px", width: "20px" }}
                        type="checkbox"
                        id="rememberMe"
                        value={values.rememberMe}
                        className="form-check-input"
                        onChange={handleChange}
                        onBlur={handleBlur}
                      />
                      {errors.rememberMe && touched.rememberMe ? (
                        <p
                          style={{
                            color: "red",
                            fontSize: "small",
                            margin: "0",
                          }}
                        >
                          {errors.rememberMe}
                        </p>
                      ) : null}
                      <label className="form-check-label" htmlFor="rememberMe">
                        Remember me
                      </label>
                    </div>
                  </div>
                  <div className="col col-6 mb-3 text-end">
                    <label onClick={() => handleForgotPasswordModal()} className="text-end">
                      <a
                        href="javascript:void(0)"
                        data-bs-toggle="modal"
                        data-bs-target="#ForgotPassModal"
                      >
                        Forgot Password?
                      </a>
                    </label>
                   
                  </div>
                </div>
                <div className="col col-12 text-center">
                  <button
                    type="submit"
                    className="btn d-block w-100 btn-outline-primary py-3"
                    onClick={handleSubmit}
                  >
                    <i className="fa-solid fa-arrow-right-to-bracket me-2"></i>
                    Log in
                  </button>
                </div>
                
              </div>
            );
          }}
        </Formik>
      </Modal>
     
    </div>
  );
};

export default Login;